.logo{
    display: flex;
    flex-direction: row;
    column-gap: 15px;
}
.logo-rgm{
    padding-top: 10px;
}
.header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.side-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    column-gap: 20px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.library-image{
    width: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
}
.introduction-div{
    position: absolute;
    text-align: center;
    color: black;
    font-weight: bold;
    opacity: 1;
    line-height: 60px;
}
.library{
    opacity: 0.5;
}
.quotes{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
}
.quote{
    display: flex;
    flex-direction: column;
    row-gap: 1px;
    padding-left: 15px;
}
.image-quote{
    
    animation: float-img 4s ease-out infinite;
}

@keyframes float-img{
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-30px);
    }
    100%{
        transform: translateY(0px);
    }
}
.desc{
    color: #4dc3ff;
}
.description{
    display: flex;
    flex-direction: row;
   column-gap: 70px;
   flex-wrap: nowrap;
}
.description-text{
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 30px;
    box-shadow: 10px 10px 15px gray;
}
details{
    width: 300px;
    border: 10px;
    margin-left: 20px;
    background-color: lightgray;
    padding-top: 7px;
    padding-bottom: 7px;
    padding-left: 10px;
    border-radius: 5px;

}
.branch{
    color: #4dc3ff;
}
.branches{
    
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
}
.dept-details{
    text-align: left;
    padding-top: 40px;
}
.dept-details2{
    text-align: left;
    padding-top: 40px;
}
.catalog-images{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    column-gap: 46px;
    
}
.catalog-text{
    text-align: center;
    color: #7CB9E8;
   
    
}
.catalog-images2{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    column-gap: 46px;
}
.pdf-text,.con-text{
    padding-left: 10px;
     font-family: cursive;
     color: rgb(74, 160, 189);
    
}
.query-text{
    padding-left: 60px;
     font-family: cursive;
     color: rgb(74, 160, 189);
    
}
.ref-text{
    font-family: cursive;
    color: rgb(74, 160, 189);
   
}
footer {
    text-align: center;
    padding: 3px;
    background-color: black;
    color: white;
  }