.heading-about{
    color: white;
    background-color: rgb(44, 139, 227);
    
    text-align: center;
}
.image-about{
    opacity: 0.5;
}
.overlap{
    display: flex;
    justify-content: center;
    align-items: center;
}

.div-about{
    display: flex;
    flex-direction: row;
    position: absolute;
    column-gap: 200px;
    padding: 50px;
}
.text-about{
    padding: 20px;
    padding-bottom: 0px;
    text-align: left;
    border: 10px double rgb(44, 139, 227);
}
