.heading-contact{
    color: white;
    text-align: center;
    background-color: rgb(44, 139, 227);
}


.flex-contact{
  
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 30px;
    padding-left: 0px;
}
.mail-contact{
    text-align: left;
    background-color: rgb(44, 139, 227,0.5);
    padding: 20px;
    
}
.flex-contact-2,.flex-contact-3{
    
    padding-top: 70px;
    letter-spacing: 1px;
    
}